
.progress-linear {
  margin: 0.5rem 0;
}
.foto-wrapper {
  width: 250px;
  height: 250px;
  display: inline-block;
  border-radius: 50%;
  margin-left: calc(50% - 125px);
  border: 1px dotted #b9b9b9;
  position: relative;
}
.foto-wrapper .foto {
  width: 230px;
  height: 230px;
  margin: 10px;
  display: inline-block;
  border-radius: 50%;
  background: url("/static/jeferson.jpeg");
  background-size: cover;
  background-position: center center;
}
#linha-wrapper {
  position: relative;
}
#linha-wrapper .linha {
  margin-top: -5px;
  position: absolute;
  top: 0;
  bottom: 0;
  width: 10px;
  background: transparent;
  left: calc(50% - 5px);
  border-left: 1px dotted #b9b9b9;
  border-right: 1px dotted #b9b9b9;
}
.projects {
  background-color: #b9b9b9;
}


